.categories {
    padding: 50px 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #f7f8f8;
}
@media (max-width: 480px) {
    .categories {
        padding: 50px 20px;
   }
}
.categories h1 {
    color: #24355a;
    font-size: 40px;
    font-weight: 800;
    margin-bottom: 15px;
}
.categories p {
    max-width: 700px;
    font-size: 20px;
}
.categories .categories_list {
    display: flex;
    flex-wrap: wrap;
    padding: 50px 0;
    justify-content: center;
    gap: 20px;
}
.categories .categories_list a {
    text-decoration: none;
}
.categories .categories_list .category {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 250px;
    height: 200px;
    cursor: pointer;
}
.categories .categories_list .category img {
    position: absolute;
    width: 100%;
    height: 100%;
}
.categories .categories_list .category .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.55);
    transition: 0.3s ease-in-out;
}
.categories .categories_list .category_text {
    position: relative;
    color: white;
}
.categories .categories_list .category_text_icon {
    font-size: 45px;
}
.categories .categories_list .category_text p {
    font-weight: 600;
}
.categories .categories_list .category:hover .overlay {
    width: 80%;
    height: 80%;
}
