.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    padding: 10px 70px;
    background-color: #6d6875;
}
@media (max-width: 1000px) {
    .footer {
        padding: 10px 30px;
   }
}
.footer_left {
    max-width: 400px;
}
.footer_left img {
    max-width: 150px;
    margin-bottom: 20px;
}
.footer_left_socials {
    display: flex;
    gap: 25px;
    margin-top: 20px;
}
.footer_left_socials_icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #f8395a;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
.footer_left_socials_icon:hover {
    transform: scale(1.2);
}
.footer_center {
    text-decoration: none;
}
.footer_center ul {
    list-style: none;
    margin-top: 20px;
    cursor: pointer;
}
.footer_center ul li:hover {
    color: #f8395a;
}
.footer_center ul a {
    text-decoration: none;
    color: black;
}
.footer_center ul a:hover {
    color: #f8395a;
}
@media (max-width: 720px) {
    .footer_center {
        display: none;
   }
}
.footer_right {
    max-width: 350px;
}
.footer_right h3 {
    margin-bottom: 20px;
}
.footer_right_info {
    display: flex;
}
.footer_right_info p {
    margin-left: 20px;
    margin-bottom: 15px;
}
@media (max-width: 480px) {
    .footer_right {
        display: none;
   }
}

